.app {
  background-color: rgb(161, 161, 161);
  display:flex;
  justify-content: center;
  align-items: center;
}

.rectangle {
  display: flex;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  
}

.control {
  margin-bottom: 2px;
  height: 348px;
  width: 250px;
  background-color: white;
}

.checklist-container {
  height: 700px;
  width: 500px;
  background-color: white;
  border-radius: 10px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.checklist {
  list-style-type: none;
  padding: 0;
}

.checklist li {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.separador-check {
  margin-right: 100px;
  width: 200px;
  display: flex;
  flex-direction: column;
}

.checklist-text {
  font-size: 3vw; /* Tamaño del texto relativo a la pantalla */
  color: black;
  margin-right: 10px; /* Espacio entre el texto y el check */
}
.checklist-subtext {
  font-size: 1.5vw; /* Tamaño del texto relativo a la pantalla */
  color: black;
  margin-top: 5px; /* Espacio entre el texto y el check */
}

.checklist-label {
  display: flex;
  align-items: center;
  position: relative;
}

.checklist-label input[type="checkbox"] {
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.checkmark {
  width: 24px; /* Tamaño del checkmark */
  height: 24px; /* Tamaño del checkmark */
  border: 2px solid black;
  border-radius: 4px; /* Hace que el checkmark sea un cuadrado */
  position: relative;
}

.checkmark::after {
  content: '✓'; /* Agrega un tick (✓) al checkmark */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 23px; /* Tamaño del tick relativo al checkmark */
  color: green;
  opacity: 0;
}

.checklist-label input[type="checkbox"]:checked + .checkmark::after {
  opacity: 1;
}

.paridades {
  height: 350px;
  width: 250px;
  background-color: white;
}

.tratados {
  height: 400px;
  width: 750px;
  background-color: white;
}

.lista {
  height: 400px;
  width: 750px;
  background-color: white;
}

.columna{
  flex: 1;
  padding: 1px;
}

.fila{
  flex: 1;
}

.container-trat-list{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.container-control-check-parid{
  margin-top: 20px;
  display: flex;
}

.separador{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.conversor{
  display: flex;
  flex-direction: column;
  margin: 15px;
}